// import logo from './logo.svg';
import './App.css';
import React, { useEffect, useState } from 'react';


const SHEET_KEY = '1pMkhEnCmNodO3dGAG6bJ9GdVNnmtSCcZEvhFEqQIY1M';
const URL = `https://spreadsheets.google.com/feeds/cells/${SHEET_KEY}/1/public/full?alt=json`;
const fetchData = async () => {
  return await fetch(URL).then(res => {
    return res.json();
  })
}


function App() {
  const [openExam, setOpenExam] = useState(false);
  const [data, setData] = useState([]);
  const [name, setName] = useState("");
  const [isSubmit, setIsSubmit] = useState(false);
  const [falseCount, setFalseCount] = useState(0);
  const [openCopyPenalty, setOpenCopyPenalty] = useState(false);
  const [loading, setLoading] = useState(true);
  const [falseData, setFalseData] = useState([]);
  const [finalCopyPenalty, setFinalCopyPenalty] = useState(false);

  useEffect(() => {

    fetchData().then(res => {
      const mapping = [];

      const entry = res.feed.entry.map(i => i.gs$cell);

      entry.forEach(i => {
        if (i.row > 1) {
          const index = i.row - 2;
          if (!mapping[index]) mapping[index] = { value: "", copy: "", correct: true, ok: false };

          switch (i.col) {
            case '1': mapping[index].q = i['$t'].trim(); break;
            case '2': mapping[index].a = i['$t'].trim(); break;
            default: break;
          }
        }
        else {
          if (i.col === '5') setOpenExam(i['$t'].trim().toLowerCase() === 'on' ? true : false);
        }
      })
      setData(mapping);
      setLoading(false);

    })

  }, [])

  const changeValue = (index, elm) => {
    const clone = [...data];
    clone[index].value = elm.target.value;
    setData(clone);
  }

  const onSubmit = () => {
    if (!name) {
      window.alert("Bạn chưa nhập họ tên!");
      return;
    }

    let alert = "Bạn chắc chắn muốn nộp bài?";
    let valueNullCount = 0;
    data.forEach(item => {
      if (item.value === "") valueNullCount++;
    })
    if (valueNullCount > 0) alert = `${alert} Bạn còn ${valueNullCount} câu chưa trả lời!`;

    if (window.confirm(alert)) {
      let clone = [...data];
      let falseDataTemp = [];
      falseDataTemp = data.filter((item, index) => {
        if (item.value.trim().toLowerCase() !== item.a.toLowerCase()) {
          clone[index].correct = false;
          return true;
        }
        return false;
      })

      setData(clone);
      setFalseData(falseDataTemp);
      setFalseCount(falseDataTemp.length);
      setIsSubmit(true);
    }
  }

  const toggleCopyPenalty = () => {
    setOpenCopyPenalty(!openCopyPenalty);
  }

  const changeName = (elm) => {
    setName(elm.target.value);
  }

  const printResult = () => {
    var mywindow = window.open('', 'PRINT');

    mywindow.document.write('<html><head><title>EXAM RESULT</title>');
    mywindow.document.write('</head><body >');
    // mywindow.document.write('<h1>' + document.title + '</h1>');
    mywindow.document.write(document.getElementById('printable').innerHTML);
    mywindow.document.write('</body></html>');

    mywindow.document.close(); // necessary for IE >= 10
    mywindow.focus(); // necessary for IE >= 10*/

    mywindow.print();
    mywindow.close();

    return true;
  }

  const changeCopyPenalty = (index, elm) => {
    const clone = [...falseData];
    clone[index].copy = elm.target.value;
    setFalseData(clone);
  }

  const onSubmitCopyPenalty = () => {
    const finalCopyPenaltyTemp = falseData.some(item => {
      const amount = occurrences(item.copy, item.a);
      if (amount < 10) return true;
      return false;
    })

    if (finalCopyPenaltyTemp) {
      window.alert("Yêu cầu mỗi từ sai phải chép lại 10 lần!");
    } else {
      setFinalCopyPenalty(true);
      setOpenCopyPenalty(false);
    }
  }

  const preventPaste = (elm) => {
    elm.preventDefault();
  }

  if (loading) {
    return (
      <div className="App">
        <h1>Loading...</h1>
      </div>
    )
  }

  if (!openExam) {
    return (
      <div className="App">
        <h1>Bài thi chưa được mở!</h1>
      </div>
    )
  }

  return (
    <div className="App">
      <h2>ENGLISH EXAM</h2>

      <div id="printable">

        <div>
          <div><b>Họ tên:</b>
            {isSubmit
              ?
              <span style={{ marginLeft: '5px' }}><b>{name}</b></span>
              :
              <input className="input-name" type="text" value={name} onChange={changeName} />
            }
          </div>
          <div>
            {isSubmit &&
              <span><b>Kết quả: {data.length - falseCount}/{data.length}</b></span>
            }
          </div>
        </div>
        {(isSubmit && falseCount > 0)
          &&
          <div>
            {finalCopyPenalty
              ? <span style={{ color: 'green' }}><b>Đã chép phạt</b></span>
              : <span style={{ color: 'red' }}><b>Chưa chép phạt</b></span>
            }
          </div>
        }
        <br />

        <div className="container-table">
          {!openCopyPenalty
            ?
            <table width="100%">
              <thead>
                <tr>
                  <th style={{ padding: '10px' }}>#</th>
                  <th style={{ padding: '10px' }}>Question</th>
                  <th style={{ padding: '10px' }}>Your Answer</th>
                  {isSubmit && <th style={{ padding: '10px' }}>Correct Answer</th>}
                </tr>
              </thead>
              <tbody>
                {data.map((item, index) =>
                  <tr key={index}>
                    <td style={{ textAlign: 'center' }}>{index + 1}</td>
                    <td style={{ textAlign: 'center' }}>
                      {item.q}
                    </td>

                    <td>
                      <input type="text"
                        style={{ width: '100%', textAlign: 'center', color: (isSubmit && !item.correct) ? 'red' : 'black'}}
                        value={item.value}
                        disabled={isSubmit}
                        onChange={changeValue.bind(this, index)}
                      />
                    </td>
                    {isSubmit &&
                      <td style={{ textAlign: 'center' }}>
                        {item.a}
                      </td>
                    }
                  </tr>
                )}
              </tbody>
            </table>
            :
            <table border="1">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Question</th>
                  <th>Copy penalty 10 times</th>
                  <th>Correct Answer</th>
                </tr>
              </thead>
              <tbody>
                {falseData.map((item, index) =>
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>
                      {item.q}
                    </td>

                    <td>
                      <textarea type="text"
                        rows={5}
                        onPaste={preventPaste}
                        value={item.copy}
                        onChange={changeCopyPenalty.bind(this, index)}
                      />
                    </td>

                    <td>
                      {isSubmit && item.a}
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          }
        </div>
      </div>

      {openCopyPenalty
        ?
        <React.Fragment>
          <button className="green" onClick={onSubmitCopyPenalty}>Hoàn tất chép phạt</button>
          <button className="red" onClick={() => { setOpenCopyPenalty(false) }}>Hủy</button>
        </React.Fragment>
        :
        <React.Fragment>
          {!isSubmit
            &&
            <button onClick={onSubmit} className="green">Nộp bài</button>
          }
          {(falseCount > 0 && !finalCopyPenalty)
            &&
            <button className="red" onClick={toggleCopyPenalty}>Bạn có {falseCount} từ phải chép phạt</button>
          }
          {isSubmit
            &&
            <button className="blue" onClick={printResult}>In kết quả</button>
          }
        </React.Fragment>
      }

    </div>
  );
}

function occurrences(string, subString, allowOverlapping) {

  string += "";
  subString += "";
  if (subString.length <= 0) return (string.length + 1);

  var n = 0,
    pos = 0,
    step = allowOverlapping ? 1 : subString.length;

  while (true) {
    pos = string.indexOf(subString, pos);
    if (pos >= 0) {
      ++n;
      pos += step;
    } else break;
  }
  return n;
}

export default App;